.input-container label{
    font-family: "Arial";
    font-size:17px;
    text-align: left;
    font-weight: bold;
    color:dimgrey;
}

.input-container input{
    height:25px;
    margin-bottom: 10px;
}

.setting-container button{
    border-radius:10px;
    font-size: 15px;
    font-weight: bold;
    transition: border 1s,opacity 1s;
    background-color: #efbca7;
    outline: none;
    box-shadow: 0 1px #999;
    transition:background-color 0.5s,transform 0.5s, opacity 1s;
}

